 @tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom modern animated background */
body {
  @apply min-h-screen overflow-hidden;
  position: relative;
  background-color: #064e3b; /* Dark green base */
}

.background-animation {
  position: absolute; 
  top: 0;
  left: 0;
  width: 200%;
  height: 200%;
  z-index: -1;
  overflow: hidden;
  pointer-events: none;
  background: 
    radial-gradient(circle at 20% 20%, #10b981 0%, transparent 50%),  /* softer green */
    radial-gradient(circle at 80% 80%, #047857 0%, transparent 50%),  /* darker green */
    radial-gradient(circle at 50% 50%, #065f46 0%, transparent 60%);
  animation: animateBackground 20s linear infinite;
  opacity: 0.15; /* lower opacity */
}
.background-photo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('gps.jpg'); /* Replace with your image path */
  background-size: cover;
  background-position: center;
  opacity: 0.5; /* 50% transparent */
  z-index: -2; /* behind the background animation (-1) */
  pointer-events: none; /* So it won't block any mouse events */
}

@keyframes animateBackground {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-25%, -25%);
  }
  100% {
    transform: translate(0, 0);
  }
}

/* Stars container */
.stars {
  position: absolute;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  overflow: visible;
  /* transition removed to allow JS smooth transform */
}

/* Single star style */
.star {
  position: absolute;
  background: white;
  border-radius: 50%;
  opacity: 0.6;
  width: 2px;
  height: 2px;
  animation: twinkle 6s infinite;
  animation-timing-function: ease-in-out;
  will-change: transform, opacity;
  transition: transform 2s ease-out;
}

/* Twinkle animation for stars */
@keyframes twinkle {
  0%, 100% {
    opacity: 0.6;
    transform: translate(0, 0);
  }
  50% {
    opacity: 1;
    transform:
      translate(var(--x1, 0), var(--y1, 0))
      translate(var(--x2, 0), var(--y2, 0))
      translate(var(--x3, 0), var(--y3, 0));
  }
}

/* Buttons container style */
.absolute.top-6.flex.gap-6.z-10 button {
  background: rgba(255 255 255 / 0.15);
  border: 1px solid white;
  color: white;
  padding: 0.5rem 1.5rem;
  border-radius: 9999px; /* full rounded */
  font-weight: 600;
  transition: transform 0.3s ease, background-color 0.3s ease;
  cursor: pointer;
  backdrop-filter: blur(10px);
}

.absolute.top-6.flex.gap-6.z-10 button:hover {
  transform: translateY(-5px) scale(1.1);
  background-color: rgba(255 255 255 / 0.3);
}

/* Login box base style */
.animate-border-glow {
  animation: borderGlow 10s ease-in-out infinite;
  border-color: #34d399;
  box-shadow: 0 0 8px 2px #34d399;
}

/* Glowing border animation */
@keyframes borderGlow {
  0%, 100% {
    border-color: #34d399;
    box-shadow: 0 0 8px 2px #34d399;
  }
  50% {
    border-color: #059669;
    box-shadow: 0 0 15px 5px #059669;
  }
}

/* Glow effect on focus */
.border-glow-active {
  animation: borderGlowActive 2s ease-in-out infinite;
  border-color: #22c55e !important;
  box-shadow: 0 0 10px 3px #22c55e;
}

@keyframes borderGlowActive {
  0%, 100% {
    border-color: #22c55e;
    box-shadow: 0 0 8px 3px #22c55e;
  }
  50% {
    border-color: #4ade80;
    box-shadow: 0 0 15px 5px #4ade80;
  }
}

/* Input styles */
input {
  background-color: rgba(255 255 255 / 0.2);
  color: white;
  border-radius: 0.375rem; /* rounded-md */
  padding: 0.5rem 0.75rem;
  border: none;
  outline: none;
  width: 100%;
}

input::placeholder {
  color: rgba(255 255 255 / 0.7);
}

input:focus {
  outline: 2px solid white;
  outline-offset: 2px;
}

/* Submit button */
button[type="submit"] {
  background-color: white;
  color: #22c55e;
  font-weight: 700;
  padding: 0.5rem 0;
  border-radius: 0.375rem;
  transition: background-color 0.3s ease;
  width: 100%;
}

button[type="submit"]:hover {
  background-color: #bbf7d0;
  color: #15803d;
}
